ACxB,qCAzKD,kBA6KI,eAAgB,CACjB,CAGH,qCAjLA,kBAmLI,eAAgB,CACjB","sourcesContent":["@import url(./assets/fonts/fonts.scss);\n\nbody {\n  // Overrides default 8px margin on some browsers\n  margin: 0px;\n  text-align: justify;\n  text-justify: auto;\n}\n\n.bordered-image {\n  border: 1px solid #b49b1c;\n}\n\n@mixin button-reset {\n  padding: 0;\n  border: none;\n  font: inherit;\n  color: inherit;\n  background-color: transparent;\n  cursor: pointer;\n}\n\n%button-shared {\n  @include button-reset;\n  background-color: #9f8a1f;\n  margin-top: 2px;\n  margin-right: 2px;\n  color: white;\n  border: none;\n  cursor: pointer;\n  \n  &:disabled {\n    background-color: #2e2c21;\n  }\n}\n\n.button {\n  @extend %button-shared;\n  padding: 16px;\n  font-size: 16px;\n}\n\n.randomize {\n  @extend %button-shared;\n  font-size: small;\n  font-weight: 300;\n  padding: 4px;\n}\n\ninput {\n    background: transparent;\n    border: none;\n    outline: none;\n    color:white;\n    font-family: 'Play', sans-serif;\n    font-size: xx-large;\n}\n\nul {\n  list-style-type: none;\n  margin:0px;\n  padding:0px 0px 0px 7px;\n}\n\n.component-wrapper {\n  display:flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n}\n\n.container {\n  background-color: #011126;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  .header {\n    width:100%;  \n    background-image: url('./assets/images/header_background.jpg');\n    background-size: cover;\n    background-position-y: 60%;\n    background-color: #011126;\n    background-color: rgb(103, 117, 117);\n    h1 {\n        color: #FFF;\n        font-family: 'Play', sans-serif;\n    }\n    text-align: center;\n    .responsive {\n      max-width: 90%;\n      max-height: 350px;\n    }\n  }\n  .widget {\n    width:100%;\n    //background-color:rgb(93, 96, 128);\n    background-color:#011125;\n    margin-top: 5px;\n    margin-bottom: 5px;\n    padding-left: 7px;\n    padding-right: 7px;\n    padding-bottom: 10px;\n  }\n}\n\n.column-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n}\n\n.fine-print {\n  font-size: small;\n}\n\n.footer {\n  padding-top: 30px;\n  padding-bottom: 30px;\n  text-align: center;\n  font-size: small;\n}\n\n.point-value {\n  font-size:xx-large;\n  color:white;\n}\n\n.career-points-container {\n  display:flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  justify-content: flex-start;\n  border: 1px solid hotpink;\n}\n\n.career-points-distributor-wrapper {\n  display:flex;\n  flex-direction: row;\n  align-items: center;\n  width:45%;\n  //border: 1px solid hotpink;\n  margin-top: 5px;\n  margin-bottom: 10px;\n  .selection-dropdown {\n    text-align: center;\n  }\n}\n\n\n.points-distributor-wrapper {\n  display:flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: space-around;\n  border: 1px solid hotpink;\n  margin-top: 5px;\n  margin-bottom: 10px;\n  padding-bottom: 3px;\n\n  .button {\n    margin-left: 3px;\n    margin-right: 3px;\n  }\n  .selection-dropdown {\n    text-align: center;\n  }\n  .dropdown-cluster-wrapper {\n    display:flex;\n    flex-direction: column;\n    align-items: flex-start;\n    width: 50%;\n  }\n  .points-distributor-category {\n    text-align: center;\n    flex-grow: 0.5;\n    width: 15%;\n    .category-name {\n      size: 16px;\n      font-weight: 700;\n    }\n  }\n  .points-distributor-category-sibling {\n    text-align: center;\n    flex-grow: 0.33;\n    width: 15%;\n    .category-name {\n      size: 16px;\n      font-weight: 700;\n    }\n  }\n}\n\n.points-distributor-flex-wrapper {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: nowrap;\n  justify-content: space-evenly;\n  align-items: center;\n  width: 27%;\n\n}\n\n.responsive {\n  max-width: 100%;\n}\n\n.career-points-distributor-category {\n  text-align: center;\n  width: 50%;\n  flex-grow: .5;\n  .category-name {\n    size: 16px;\n    font-weight: 700;\n    padding: 5px;\n  }\n}\n\n.points-distributor-control-panel {\n  button {\n    width:100%;\n    height: 50%;\n  }\n}\n\n.points-distributor-value {\n  //border: 1px dotted white;\n  flex-grow: 6;\n  width:50%;\n  padding-left: 10px;\n  padding: 3px 5px 3px 7px;\n  font-size: small;\n  font-weight: 700;\n}\n\n.selection-dropdown {\n  text-align: left;\n}\n\n.warning {\n  color: hotpink;\n}\n\n// Rules for mobile\n@media screen and (max-width: 480px) {\n  .column-container {\n    max-width: 320px;\n  }\n  .points-distributor-flex-wrapper {\n    flex-direction: column;\n    justify-content: flex-end;\n    .points-distributor-category {\n      display:flex;\n      flex-direction: column;\n      align-items: center;\n    }\n  }\n\n  input {\n    font-size: x-large;\n  }\n  .category-name {\n    font-size: small;\n  }\n  .career-points-container, .points-distributor-wrapper {\n    .button {\n      padding-left: 8px;\n      padding-right: 8px;\n    }\n  }\n}\n\n.career-points-container {\n  justify-content: center;\n}\n\n@media screen and (min-width: 480px) {  \n  .column-container {\n    max-width: 480px;\n  }\n}\n\n@media screen and (min-width: 768px) {  \n  .column-container {\n    max-width: 768px;\n  }\n}"],"sourceRoot":""}]);
